* {
    box-sizing: border-box;
}
  
body {
    display: flex;
    flex-direction: column;
    align-items: center;
    min-height: 100vh;
    
    font-family: sans-serif;
    margin: 0;
}
  
.countdown-container {
    max-width: 900px;
    min-height: 300px;
    margin: 200px auto;
    gap: 80px;
    background-color: #fff;
    box-shadow: 10px 10px 10px 10px rgba(0, 0, 0, 0.1);
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
}
  
.countdown-values {
    display: flex;
}
  
.big-text {
    font-weight: bold;
    font-size: 6.5rem;
    line-height: 1;
    margin: 10px;
}
  
.countdown-value {
    width: 120px;
    margin: 10px 20px;
    text-align: center;
}
  
.countdown-value span {
    font-size: 1.3rem;
}
  
.countdown-input {
    padding: 9px;
    font-size: 19px;
    border-width: 0px;
    background-color: #ffffff;
    color: #000000;
    border-style: solid;
    border-radius: 7px;
    box-shadow: 0px 0px 3px rgba(66, 66, 66, 0.53);
    outline: none;
}
  
.countdown-button {
    color: #050505;
    background-color: #07f21b;
    font-size: 20px;
    border: 1px solid #050505;
    border-radius: 5px;
    padding: 10px 50px;
    cursor: pointer;
    margin-bottom: 30px;
}
  
.countdown-button:hover {
    color: #050505;
    background-color: #f70a16;
}
  
.countdown-input-button {
    display: flex;
    flex-direction: column;
    gap: 12px;
}
  